.wrapper {
  width: var(--width-desktop);
  transform-origin: top left;
  overflow-x: hidden;
  overflow-y: auto;
}
@media (orientation:portrait) {
  .wrapper {
    width: var(--width-mobile);
  }
}
.wrapper__content {
  overflow-x: hidden;
  overflow-y: hidden;
  width: var(--width-desktop);
}

.container {
  display: block;
  margin: 0 auto;
  width: var(--width-desktop-safe);
  max-width: var(--maxwidth-desktop-safe);
}
@media (orientation:portrait) {
  .container {
    width: var(--width-mobile-safe);
  }
}

hr.split {
  margin: 0;
  border: 0;
  background: transparent;
}
hr.split--low {
  height: 16px;
}
hr.split--medium {
  height: 24px;
}
hr.split--high {
  height: 32px;
}
hr.split--giant {
  height: 48px;
}

.section {
  position: relative;
  display: block;
  width: var(--width-desktop);
}
@media (max-width: 700px) {
  .section {
    width: var(--width-mobile);
  }
}
@media (orientation:portrait) {
  .section {
    width: var(--width-mobile);
  }
}
.section__background {
  display: block;
  width: 100%;
}
.section__background img,
.section__background video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.section__background video {
  position: absolute;
  top: 0;
}
.section__content {
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
}
.section.autoHeight .section__background {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
}
.section.autoHeight .section__content {
  display: block;
  position: relative;
}
.section--fixedHeight > .section__background {
  position: static;
  display: block;
  width: 100%;
  height: 100%;
}
.section--fixedHeight > .section__background img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (max-width: 700px) {
  .section--fixedHeight > .section__background img {
    height: auto;
  }
}
@media (orientation:portrait) {
  .section--fixedHeight > .section__background img {
    height: auto;
  }
}
.section--fixedHeight > .section__content {
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
}
.section__overlay {
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  background: rgba(0, 0, 0, 0.8) 100%; /* fallback for old browsers */
  background: -webkit-linear-gradient(rgba(0, 0, 0, 0) 20%, rgba(0, 0, 0, 0.8) 100%); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(rgba(0, 0, 0, 0) 20%, rgba(0, 0, 0, 0.8) 100%); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

