.floating {
  pointer-events: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 500;
  display: flex;
  align-items: center;
  justify-content: center;
}
.floating * {
  pointer-events: all;
}

.scrollwatch-pin {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 1px;
  height: 1px;
  background: transparent;
  pointer-events: none;
}

@keyframes shine {
  100% {
    filter: brightness(120%);
  }
}
@keyframes shine_scale {
  100% {
    filter: brightness(90%);
    transform: scale(1.1);
    transform-origin: center center;
  }
}
@keyframes shine_move {
  100% {
    filter: brightness(110%);
    transform: translateX(12px);
  }
}
.popup {
  pointer-events: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 500;
  display: flex;
  align-items: center;
  justify-content: center;
}
.popup__background, .popup__background--unclosable {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  background: rgba(0, 0, 0, 0.8);
  width: 100%;
  height: 100vh;
  transition: opacity 1s;
}
.popup__content {
  opacity: 0;
  visibility: hidden;
  z-index: 2;
  transition: all 0.2s;
  transform: scale(0);
}
.popup__tool {
  pointer-events: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 500;
  padding: 5px;
  display: flex;
  opacity: 0;
  visibility: hidden;
}
.popup__close {
  pointer-events: all;
  background: #000;
  position: absolute;
  right: 0%;
  top: 0%;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  width: 42px;
  height: 42px;
}
.popup.active {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}
.popup.active .popup__background, .popup.active .popup__background--unclosable {
  opacity: 1;
  visibility: visible;
}
.popup.active .popup__content {
  opacity: 1;
  visibility: visible;
  transform: scale(1);
}
.popup.active .popup__tool {
  opacity: 1;
  visibility: visible;
}

.popup--open-image .popup__content .image img {
  display: block;
  max-width: 100%;
  max-height: 100%;
}

.popup--open-video .popup__content {
  width: 80%;
  height: 60%;
}
.popup--open-video .popup__content .embed {
  width: 100%;
  height: 100%;
}
.popup--open-video .popup__content .embed iframe {
  display: block;
  width: 100%;
  height: 100%;
}